.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

fieldset {
  background-color: #3e444e;
  padding: 20px;
  margin: 20px;
  width: 350px;
  text-align: left;
}

legend {
  text-align: left;
  font-weight: 600;
  color: whitesmoke;
}

fieldset label {
  text-align: left;
}

input {
  margin-bottom: 8px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
